import { Component, h, Prop, Element } from '@stencil/core';

@Component({
      tag: 'ks-simpleform'
})
export class SimpleForm {
    @Prop() action: string;
    @Prop() method: string;

    @Element() root: HTMLElement;

	render() {
		return(
            <form action={this.action} method={this.method} onSubmit={(event)=>this.Submit(event)}>
                <slot/>
            </form>
		);
    }

    async Submit(event: Event) {
        event.preventDefault();

        const form = this.root.querySelector("form");
        const inputs = form.querySelectorAll("ks-input-text, ks-input-textarea, ks-input-check, ks-input-number, ks-input-date, ks-input-select");

        let valid = true;
        for (let i = 0; i < inputs.length; i++) {
            const input = inputs[i] as any;
            
            await input.Validate();

            const isValid = await input.IsValid()

            if(!isValid) {
                valid = false;
            }
        }

        if(valid) form.submit();
    }
}
